.App {
	display: flex;
	align-items: center;
	justify-content: center;
}

.AppLogo {
	height: 26vmin;
	pointer-events: none;
	margin-bottom: 10vmin;
}

@media (prefers-reduced-motion: no-preference) {
	.AppLogo {
		animation: App-logo-spin infinite 4s linear;
	}
}

.AppHeader {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(6px + 2vmin);
	color: white;
}

.AppLink {
	padding: calc(1vmin + 6px) calc(1vmin + 24px);
	background: linear-gradient(0.25turn, rgba(148, 127, 238, 0.2), rgba(244, 93, 162, 0.11));
	font-size: calc(1vmin + 6px);
	border-radius: 4px;
	color: #e65bff;
	text-decoration: none;
	font-weight: 100;
	transition: 0.5s;
}

.AppLink:hover {
	background: rgba(148, 127, 238, 0.14);
}

.AuthorLink {
	margin-top: calc(2vmin);
	font-size: calc(6px + 1vmin);
	color: white;
	font-weight: 100;
	opacity: 0.9;
}

.AuthorLink > a {
	color: #e65bff;
	text-decoration: none;
}

.AuthorLink > a:hover {
	text-decoration: underline;
}

.AppScreen {
	position: absolute;
	z-index: -1;
	width: 30vmin;
	border-radius: 8px;
	transform: skewY(-10deg) translateX(-30vmin) translateY(-11vmin);
	filter: blur(calc(1vmin));
}

.AppScreenBefore {
	position: absolute;
	z-index: -1;
	width: 30vmin;
	border-radius: 8px;
	transform: skewY(-10deg) translateX(calc(-29.6vmin)) translateY(calc(-10.2vmin));
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
